@import '../../utils/breakpoints';

.transfers {
  display: grid;
  grid-template-columns: minmax(200px ,302px) minmax(200px ,662px)  minmax(150px, 260px);
  grid-template-rows: auto-fit  ;
  column-gap: 16px;
  height: 80%;
  .promo {
    background: url(../../img/promo/promo2.png) no-repeat;
    &__content {
      padding: 16px;
    }
    &__title {
      font-size: 22px;
      line-height: 125.3%;      
      color: #FFFFFF;     
      width: 100%; 
    }
    &__btn {
      background: none;
      border: 1px solid;
      width: 110px;
      height: 40px;
      padding: 0;
      margin-top: 16px;
    }
  }  
  &__operation {
    position: relative;
  }
}



@media (max-width: $mobile-width) { 
  .transfers{
    animation: openPageScaling ease-in-out 0.4s forwards;
    grid-template-columns: auto;
    .promo {
      display: none;
    }
  }
}