@import '../../utils/breakpoints';

.sideMenu {
  display: flex;
  flex-direction: column;
  &__links {
    padding: 16px;
    width: 100%;
  }
  &__title {
    font-size: 18px;
    line-height: 125.3%;
    color: #192434;
    margin-bottom: 24px;
  }
  &__link {
    display: flex;
    align-items: center;
    margin-top: 24px;
    padding-left: 16px;
    font-family: Helvetica, sans-serif;
    color: rgba(26, 41, 61, 0.83);
    &-active, &:hover {
      color: #1E4DFC;
      border-left: 3px solid #1E4DFC;
    }
    .link {
      &__title {
        font-size: 18px;
        line-height: 125.3%;
      }
      &__icon {
        display: none;
        justify-content: center;
        align-items: center;
        width: 44px;
        height: 38px;
      }
    }
  }
}

@media (max-width: $mobile-width) { 
  .sideMenu {
    margin-left: 20px;
    margin-right: 20px;
    &__links {
      padding: 11px 16px;
    }
    &__title {
      font-size: 16px;
      line-height: 125.3%;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      color: rgba(28, 46, 69, 0.6);
      font-weight: normal;
    }
    &__link {
      padding-left: 7px;
    }
    .link {
      &__icon {
        display: flex;
      }
    }
    .border-gradient {
      border-left: 0.5px solid;
      border-right: 0.5px solid;
    }
  }
}